

.game-board {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

.turn-indicator {
  text-align: center;
  padding: 15px 30px;
  border-radius: 25px;
  font-weight: bold;
  font-size: 1.2rem;
  margin-bottom: 20px;
  transition: all 0.3s;
  min-width: 200px;
}

.turn-indicator.my-turn {
  background: #4ecdc4;
  color: white;
  box-shadow: 0 4px 15px rgba(78, 205, 196, 0.3);
}

.turn-indicator.waiting {
  background: #f5f5f5;
  color: #999;
  border: 2px solid #ddd;
}

.cards-grid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  gap: 10px;
  background: rgba(255, 255, 255, 0.1);
  padding: 20px;
  border-radius: 15px;
  backdrop-filter: blur(10px);
  box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
}

@media (max-width: 768px) {
  .cards-grid {
    gap: 5px;
    padding: 10px;
  }
  
  .turn-indicator {
    padding: 10px 20px;
    font-size: 1rem;
    margin-bottom: 15px;
  }
}