.game-info {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 15px;
  padding: 20px;
  margin: 0 auto 30px;
  max-width: 600px;
  box-shadow: 0 4px 15px rgba(0,0,0,0.2);
}

.game-over-banner {
  background: linear-gradient(45deg, #667eea, #764ba2);
  color: white;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
  margin-bottom: 20px;
  animation: fadeIn 0.5s ease;
}

.game-over-banner h2 {
  margin: 0 0 10px;
  font-size: 2rem;
}

.game-over-banner p {
  margin: 0;
  font-size: 1.1rem;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.player-info {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 20px;
}

.player {
  text-align: center;
  padding: 15px 30px;
  border-radius: 10px;
  transition: all 0.3s;
  background: #f0f0f0;
}

.player.player1.active {
  background: #FFE0E0;
  border: 2px solid #FF6B6B;
  transform: scale(1.05);
}

.player.player2.active {
  background: #E0FFF8;
  border: 2px solid #4ECDC4;
  transform: scale(1.05);
}

.player.winner {
  animation: winnerPulse 2s ease-in-out infinite;
}

@keyframes winnerPulse {
  0%, 100% {
    transform: scale(1);
    box-shadow: 0 0 0 0 rgba(255, 215, 0, 0.4);
  }
  50% {
    transform: scale(1.05);
    box-shadow: 0 0 20px 10px rgba(255, 215, 0, 0.4);
  }
}

.player h3 {
  margin: 0 0 10px;
  color: #333;
}

.score {
  font-size: 2rem;
  font-weight: bold;
  margin: 0;
  color: #555;
}

.current-turn {
  text-align: center;
}

.turn-indicator {
  font-size: 1.2rem;
  font-weight: bold;
  color: #764ba2;
}

.turn-indicator.game-over {
  background: #f44336;
  color: white;
}

.reset-button, .start-button {
  background: linear-gradient(45deg, #667eea, #764ba2);
  color: white;
  border: none;
  padding: 12px 30px;
  font-size: 1rem;
  border-radius: 25px;
  cursor: pointer;
  transition: all 0.3s;
  font-weight: bold;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.reset-button:hover, .start-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(0,0,0,0.3);
}

.reset-button:active, .start-button:active {
  transform: translateY(0);
}

.player.is-me {
  box-shadow: 0 0 0 3px #667eea;
}

.connection-status {
  font-size: 0.8rem;
  margin-top: 5px;
}

.connection-status.connected {
  color: #4ecdc4;
}

.connection-status.disconnected {
  color: #ff6b6b;
}

.targets-remaining {
  font-size: 14px;
  color: #666;
  margin: 5px 0;
}

.player.is-me .targets-remaining {
  color: #4CAF50;
  font-weight: bold;
}

@media (max-width: 768px) {
  .player-info {
    flex-direction: column;
    gap: 15px;
  }
  
  .player {
    width: 100%;
    max-width: 200px;
  }
}